<script>
import axios from "axios";
import {
    Validator
} from "vee-validate";
import Loading from "@/components/helper/loading";
// Function Validate Form
const dict = {
    custom: {
        name: {
            required: "ກະລຸນາປ້ອນຊືາຮ້ານກ່ອນ..."
        },
        mobile: {
            required: "ກະລຸນາປ້ອນເບີໂທລະສັບກ່ອນ..."
        },
        email: {
            required: "ກະລຸນາປ້ອນ Email ກ່ອນ..."
        },
        address: {
            required: "ກະລຸນາປ້ອນທີ່ຕັ້ງຮ້ານກ່ອນ..."
        },
        password: {
            required: "ກະລຸນາປ້ອນລະຫັດຜ່ານກ່ອນ..."
        },
        // logo: {
        //   required: "ກະລຸເລືອກ logo ຮ້ານກ່ອນ..."
        // }
    }
};
Validator.localize("en", dict);
export default {
    components: {
        Loading
    },
    data() {
        return {
            showModalFlag: false,
            showModalEditFlag: false,
            showModalConfirm: false,
            okPressed: false,
            selectedFile: "",
            loading: false,
            restaurant: {},
            listRestaurants: []
        };
    },

    methods: {
        showModal() {
            this.okPressed = false;
            this.showModalFlag = true;
            this.restaurant = {};
        },
        showModalEdit(index) {
            this.restaurant = {
                ...this.listRestaurants[index]
            };
            this.okPressed = false;
            this.showModalEditFlag = true;
        },
        showModalDelete(index) {
            this.restaurant = {
                ...this.listRestaurants[index]
            };
            this.showModalConfirm = true;
        },
        okModal() {
            this.okPressed = true;
            this.showModalFlag = false;
            this.showModalEditFlag = false;
        },
        cancelModal() {
            this.okPressed = false;
            this.showModalFlag = false;
            this.showModalEditFlag = false;
            this.showModalConfirm = false;
        },
        AddRestaurant() {
            this.$validator.validateAll().then(result => {
                if (result) {
                    this.SubmitData();
                    return;
                }
            });
        },
        // Add Restaurant
        SubmitData() {
            this.restaurant.logo = this.selectedFile;
            var config = {
                headers: {
                    Authorization: "Bearer " + this.$store.getters.getToken
                }
            };
            axios
                .post(this.Url + "add-restaurants", this.restaurant, config)
                .then(() => {
                    (this.restaurant = {}),
                    this.cancelModal();
                    this.getData();
                });
        },

        // upload file
        UploadFiles(file) {
            let formData = new FormData();
            formData.append("imageFile", file);
            axios.post(this.Url + "upload-file/", formData).then(res => {
                this.selectedFile = res.data.fileName;
            });
        },
        ClickOnChange() {
            this.UploadFiles(this.$refs.file.files[0]);
        },
        // Get data
        getData() {
            this.loading = true;
            var config = {
                headers: {
                    Authorization: "Bearer " + this.$store.getters.getToken
                }
            };
            axios.get(this.Url + "list-restaurants", config).then(res => {
                this.loading = false;
                this.listRestaurants = res.data.restaurant;
                this.loading = false;
            });
        },
        // Update
        updateSubmit(id) {
            this.restaurant.logo = this.selectedFile;
            var config = {
                headers: {
                    Authorization: "Bearer " + this.$store.getters.getToken
                }
            };
            axios
                .post(this.Url + "edit-restaurants/" + id,
                    this.restaurant, config)
                .then(() => {
                    (this.restaurant = {}), this.cancelModal();
                    this.getData();
                });
        },
        // Delete

        deleteRestaurant(id) {
            var config = {
                headers: {
                    Authorization: "Bearer " + this.$store.getters.getToken
                }
            };
            axios.get(this.Url + "delete-restaurants/" + id, config).then(() => {
                this.getData();
                this.showModalConfirm = false;
            });
        }
    },
    created() {
        this.$validator.localize("en", dict);
        this.getData();
    },
};
</script>

